<template>
    <div class="widgetWrapper">
        <div>
            <Plotly id="Plot" :data="widgetData" :layout="widgetLayout" :responsive="true"></Plotly>
        </div>
        <div class="widget-manage-btn">
            <button type="submit" @click="fillModal(index)" class="btn btn-primary widget-edit-btn"
            data-toggle="modal" data-target="#AddWidgetModal">Edit</button>
            <button type="submit" @click="deleteWidget(index)" class="btn btn-danger widget-delete-btn">Delete</button>
        </div>
    </div>
</template>


<script>

import { Plotly } from 'vue-plotly';
import {GenerateWidget} from "../../assets/js/WidgetGenerator";

export default {
    
    name: 'WidgetItem',

    props: ['widgetMetaData', 'index'],

    components: {
        Plotly,
    },

    data() {
        return {
            widgetData: [],
            widgetLayout: {},
            gridDimensions: this.widgetMetaData.formObject.layout.widgetOptions //Watch this to update layout of plotly 
        }
    },

    methods: {

        fillModal(index)
        {
            this.$store.commit('setWidgetMode', index);
            this.$bus.$emit('FILL_Widget_MODEL', this.widgetMetaData);
        },

        deleteWidget(index){
            this.$emit('DELETE_WIDGET', index);
        },
        
        async updateWidgetData() {
            
            let data = await GenerateWidget(this.widgetMetaData, '');
            
            this.widgetData = [];
            this.widgetData = data.widgetData;
            this.widgetLayout = data.widgetLayout;
            this.gridDimensions = this.widgetMetaData.formObject.layout.widgetOptions //Again assigning it to updated gridDimention else it wont get watched

        }
      
    },

    watch: {
        
        widgetMetaData: async function() {
            this.updateWidgetData();
        },

        gridDimensions: {
            
            deep: true,

            handler() {
                this.updateWidgetData();
            }
        }

    },

    created() {
        this.updateWidgetData();
        
        // setInterval(() => { 
        //     this.widgetData = [];
        //     let a = [];
        //     let b = [];
        //     for (let i = 0; i < 5; i++) {
        //         a.push(Math.random() * 100);              
        //         b.push(Math.random() * 100);              
        //     }
        //     this.widgetData = [{x: a, y: b}]
        // }, 800);
        console.log(this.widgetMetaData);
        setInterval(() => { 
            if (this.widgetMetaData.fetcher.policy == 'interval') {
                this.updateWidgetData();             
            }
        }, this.widgetMetaData.fetcher.interval);
    },

}

</script>
